/* ServicesSection.css */

.services {
  padding: 20px 0;
  background-color: #f8f8f8;
}

.container {
  width: 100%;
  margin: 0 auto;
}

h2 {
  font-size: 36px;
  text-align: center;
  margin-bottom: 50px;
  color: #24416B;
  text-transform: uppercase;
}

.card {
  background-color: transparent;
  border: none;
  border-radius: 8px;
  transition: all 0.3s ease;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.card-img-services {
  object-fit: cover;
  height: auto;
  width: 100%;
  max-height: 250px; /* Set the maximum height to 300px */
  border-radius: 10px;
}

.card-body {
  padding-top: 20px;
}

.card-title {
  font-size: 24px;
  color: #24416B;
  margin-bottom: 10px;
}

.card-text {
  color: #777;
}

.btn-primary {
  display: inline-block;
  background-color: #24416B;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.btn-primary:hover {
  background-color: #0056b3;
}

/* New Animation Styles */
.fade-in-up {
  animation: fadeInUp 0.8s ease;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* New Hover Animation */
.card:hover {
  transform: scale(1.05);
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
}

@media screen and (max-width: 768px) {
 .col-md-4{
  padding: 5px;
 }
}


